import React, { useState, useEffect } from "react";
import axios from "axios";
import TrendContent from "../Components/TrendContent";
import Paginationn from "../Components/Pagination/Paginationn";

const Trending = (props) => {
  const [page, setPage] = useState(1);
  const [content, setContent] = useState([]);

  const fetchTrending = async () => {
    const { data } = await axios.get(
      `https://api.themoviedb.org/3/trending/all/week?api_key=f2aa6bc832a0aff8320d47cfe309bf00&page=${page}`
    );
    setContent(data.results);
  };

  useEffect(() => {
    fetchTrending();
    // eslint-disable-next-line
  }, [page]);


  const styles = {
    backgroundImage: `url('https://cdn.dribbble.com/users/556705/screenshots/5590960/media/df01217a09d807b3b0e31db748257963.png')`,
    backgroundSize: "100% 100%",
    backgroundRepeat: "no-repeat",
    filter: "brightness(70%)",
    width: "100%",
  };

  return (
    <div>
      <div style={styles} className="main"></div>
      <span className="pagesTitle">Trending</span>
      <div className="trending">
        {content &&
          content.map((c, key) => (
            <TrendContent
              content={c}
              key={c.id}
              id={c.id}
              poster={c.poster_path}
              title={c.title || c.name}
              date={c.first_air_date || c.release_date}
              media_type={c.media_type}
              vote_average={c.vote_average}
            />
          ))}
      </div>
      <Paginationn setPage={setPage} />
    </div>
  );
};

Trending.propTypes = {};

export default Trending;
